/* Cart.css */
.cart {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  margin-top: 20px;
  width: 50%;
  margin: 15px;

}

.cart-header{
  display: flex;
  justify-content: space-between;
  margin: 10px;
}
 
.cart h2 {
  margin-bottom: 10px;
}
 
.cart ul {
  list-style: none;
  padding: 0;
}
 
.cart li {
  margin-bottom: 10px;
}
 
.cart-item {
  align-items: center;
}
 
.cart-item img {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  margin-right: 10px;
}
 
.item-details p {
  margin: 0;
}
 
.cart-total {
  margin-top: 10px;
  margin-bottom: 10px;
}
 
.cart button {
  margin-top: 10px;
  background-color: white;
  color: black;
  border: 1px solid;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  cursor: pointer;
  border-radius: 4px;
}
 
.cart button:hover {
  cursor: pointer;
  background-color: black;
  color: white;
}
 
.order-now {
  background-color: #2196f3;
  margin-left: 10px;
}