form {
    border: 1px solid;
    box-shadow: 5px 10px;
   
    padding: 50px;
    display: flex;
    flex-direction: column;
  }
   
  .form input {
    padding: 8px;
    width: 200px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 14px;
    margin-block: 10px;
  }
   
  .form button {
    background-color: black;
    color: white;
    border: 1px solid;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    border-radius: 4px;
    margin-top: 50px;
    float: inline-end;
  }
   
  .form button:hover {
    cursor: pointer;
    background-color: white;
    color: black;
  }